<template>
  <div>
    <div>
      {{uploadSingleValue}}
      <v-upload-s v-model="uploadSingleValue" />
    </div>
    <div>
      {{uploadSingleValue}}
      <v-upload-s v-model="uploadSingleValue" is-file />
    </div>
    <div>
      <div>
        {{uploadMultipleValue}}
        <v-upload-m v-model="uploadMultipleValue" :multiple="true" />
      </div>
      <div>
        {{uploadMultipleValue}}
        <v-upload-m v-model="uploadMultipleValue" is-file />
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      uploadSingleValue: '',
      uploadMultipleValue: []
    }
  }
}
</script>

<style>
</style>



